header {
  padding-top: 15px;
  padding-bottom: 15px;
  @include mq(mobile) {
    position: fixed;
    transition: top 0.6s ease-in-out;
    left: 0;
    right: 0;
    z-index: 5000;
    z-index: 5000;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  @include mq(tab) {
    padding-top: 15px;
    padding-bottom: 10px;
  }
  .header-main {
    display: flex;
    align-items: center;
    justify-content: space-between;

    a {
      display: inline-block;
      font: 18px $regular;
      color: white;
      display: block;
      border-bottom: 1px solid transparent;
      padding-bottom: 10px;
      #logo-brand {
        transition: 0.6s;
        font-size: 26px;
        line-height: 1.2;
      }
      img {
        display: inline-block;
        width: 75px;
        @include mq(mobile) {
          width: 61px;
        }
        @include mq(tab) {
          width: 61px;
        }
      }
      display: inline-block;
      @include mq(mobile) {
        font-size: 20px;
      }
      @include mq(tab) {
        font-size: 24px;
      }
    }

    .brand {
      font-size: 26px;
      line-height: 1.2;
      @include mq(mobile) {
        font-size: 22px;
      }
    }
    .nav-menu {
      display: flex;
      gap: 2.5rem;
      @include mq(mobile) {
        padding-top: 80px;
        z-index: -1;
        position: fixed;
        left: -100%;
        top: 70px;
        gap: 0;
        flex-direction: column;
        width: 100%;
        min-height: 100vh;
        text-align: center;
        transition: 0.3s;
        overflow: hidden;
        background-color: $blue;

        &.active {
          left: 0;
          @include mq(mobile) {
            //padding-top: 60px;
          }
        }
        .nav-item {
          margin: 16px 0;
          @include mq(mobile) {
            margin: 10px 0;
          }
        }
      }
      @include mq(tab) {
        z-index: -1;
        position: fixed;
        left: -100%;
        top: 100px;
        gap: 0;
        flex-direction: column;
        width: 100%;
        min-height: 100vh;
        text-align: center;
        transition: 0.3s;
        background-color: $blue;
        &.active {
          left: 0;
          padding-top: 3rem;
        }
        .nav-item {
          margin: 10px 0;
        }
      }
    }
    .nav-item {
      &:hover {
        color: #f6fe01;
      }
      &.active {
        color: #f6fe01;
        border-bottom: 1px solid #f6fe01;
        padding-bottom: 10px;
      }
      @include mq(tab) {
        line-height: 2;
      }
      .nav-link {
        display: inline-block;
        position: relative;
        &:after {
          content: "";
          position: absolute;
          width: 100%;
          transform: scaleX(0);
          height: 2px;
          bottom: -18px;
          left: 0;
          background-color: white;
          transform-origin: bottom right;
          transition: transform 0.25s ease-out;
        }
        &:hover::after {
          transform: scaleX(1);
          transform-origin: bottom left;
        }
      }
    }

    .hamburger {
      display: none;
      cursor: pointer;
      .bar {
        display: block;
        width: 25px;
        height: 3px;
        margin: 5px auto;
        transition: all 0.3s ease-in-out;
        background-color: white;
        &:nth-child(1) {
          opacity: 0;
        }
        @include mq(tab) {
          width: 35px;
        }
      }
      @include mq(mobile) {
        display: block;

        &.active .bar:nth-child(1) {
          opacity: 1;
          transform: translate(0px, 8px) rotate(45deg);
        }
        &.active .bar:nth-child(2) {
          opacity: 0;
        }
        &.active .bar:nth-child(3) {
          transform: translate(0px, -8px) rotate(-45deg);
        }
      }
      @include mq(tab) {
        display: block;

        &.active .bar:nth-child(1) {
          opacity: 1;
          transform: translate(0px, 8px) rotate(45deg);
        }
        &.active .bar:nth-child(2) {
          opacity: 0;
        }
        &.active .bar:nth-child(3) {
          transform: translate(0px, -8px) rotate(-45deg);
        }
      }
    }
  }
}

