/* MODAL */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.modal-content {
  background-color: #29292e;
  padding: 30px 20px;
  padding-bottom: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  font-family: $regular;
  position: relative;
  min-width: 240px;
  color: white;
}

.modal-close-button {
  span {
    font-size: 26px;
  }
  @include flexbox();
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 18px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  background: #efefef;
  border-radius: 50%;
  color: gray;
  &:hover {
    color: black;
  }
}

.qr-code{
  max-width: 320px;
  margin: 0 auto;
}

.container {
  a{
    word-wrap: break-word;
  }
}

.qr-wallet{
  text-align: center;
  h4{
    font-size: 22px;
    margin-bottom: 20px;
    font-family: $regular;
  }
  .qr-wrap{
    margin-bottom: 20px;
  }
  @include mq(mobile){
    .wallet-address{
      font-size: 11px;
    }
  }
}